<template>
  <div id="summary">
    <!-- 操作区 -->
    <el-row class="text-right">
      <el-button type="primary" @click="insert">添加</el-button>
      <el-button type="danger" @click="remove">删除</el-button>
    </el-row>
    <!-- 表格区 -->
    <el-row id="plan">
      <el-table @select="rowclick" border :data="tableData">
        <el-table-column label="纸箱采购计划单">
          <el-table-column type="selection" width="50"></el-table-column>
          <el-table-column type="index" label="序号" width="50"></el-table-column>
          <el-table-column
            :label="item.label"
            :width="item.width"
            v-for="item in tableHeaderItems1"
            :key="item.index"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row[item.prop]"></el-input>
            </template>
          </el-table-column>
           <el-table-column label="纸箱照片" width="200">
            <el-upload
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-table-column>
          <el-table-column
            :label="item.label"
            :width="item.width"
            v-for="item in tableHeaderItems2"
            :key="item.index"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row[item.prop]"></el-input>
            </template>
          </el-table-column>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <!-- 上传图片区 -->
      <el-col class="text-left" :span="16">
        <el-upload
          action="https://jsonplaceholder.typicode.com/posts/"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible" size="tiny">
          <img width="100%" :src="dialogImageUrl" alt="" />
        </el-dialog>
      </el-col>
      <!-- 按钮区 -->
      <el-col class="text-right" :span="8">
          <el-button type="primary" @click="submit">提交</el-button>
          <el-button type="warning" @click="cancel">取消</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {
  InsertOrUpdateBox,
  GetRelatedBoxs,
  DeleteBox,
} from "network/box.js";
import {arraySort} from "service/dateUtil.js"

import {TableItem} from "service/Business/box.js"

export default {
  data() {
    return {
      tableData: [
      ],
      tableHeaderItems1: [
        {
          label: "订单号",
          width: "150",
          prop: "orderNumber",
        },
        {
          label: "业务员",
          width: "150",
          prop: "salesManName",
        },
        {
          label: "客户",
          width: "150",
          prop: "customerName",
        },
        {
          label: "货号",
          width: "150",
          prop: "productId",
        },
        {
          label: "颜色/尺码",
          width: "150",
          prop: "colorAndSize",
        },
        {
          label: "名称及规格",
          width: "150",
          prop: "boxNameAndStandard",
        }],
      tableHeaderItems2:[
        {
          label: "数量",
          width: "150",
          prop: "number",
        },
        {
          label: "采购方",
          width: "150",
          prop: "purchaser",
        },
        {
          label: "供应商",
          width: "150",
          prop: "supplier",
        },
        {
          label: "交期",
          width: "150",
          prop: "requiredTime",
        },
        {
          label: "净重(kg)",
          width: "150",
          prop: "netWeight",
        },
        {
          label: "毛重(kg)",
          width: "150",
          prop: "grossWeight",
        },
        {
          label: "PO",
          width: "150",
          prop: "poNumber",
        },
        {
          label: "CPID",
          width: "150",
          prop: "cpidNumber",
        },
        {
          label: "备注",
          width: "150",
          prop: "comment2",
        },
        {
          label: "下单日期",
          width: "150",
          prop: "creationTime",
        },
        {
          label: "单价(元/个)",
          width: "150",
          prop: "price",
        },
      ],
      selectData: [],
      deleteData: [],
      dialogImageUrl:'',
      dialogVisible:false,
      isContainBlank:false,
      getRelatedBox: {
        guid: "",
        pageSize: 0,
        pageNumber: 10,
      },
    };
  },
  created(){
    this.inital();
  },
  methods:{
    //初始化
    inital(){
       //判断新建还是编辑
      //新建
      if(this.$route.query.iid === undefined){}
      //编辑
      else{this.getRelatedBox.guid = this.$route.query.iid;
      GetRelatedBoxs(this.getRelatedBox).then((res) => {
        this.tableData = res.result.boxDto;
        this.tableData.forEach((item, index) => {
          item.index = index;
        });
      })}
    },
    //选择框
    rowclick(selection, row) {
      this.selectData = selection;
    },
    //添加按钮
    insert(){
      const tableItem = new TableItem(this.getRelatedBox.guid);
      this.tableData.push(tableItem);
      this.tableData.forEach((item, index) => {
          item.index = index;
      });
    },
    //删除按钮
    remove() {
      const data = this.selectData;
      //判断是否为空
      if (data.length == 0) {
        this.$message.error("请先选择删除项！");
      } else {
        //arr存放传给后台删除id，arr1存放界面删除的索引
        let arr = new Array(data.length);
        let arr1 = new Array(data.length);
        data.forEach((item, index) => {
          arr[index] = item.box_Order_Detail_Id;
          arr1[index] = item.index;
        });
        //排序
        arr1 = arraySort(arr1);
        //对界面数据进行删除
        arr1.forEach((item) => {
          this.tableData.splice(item, 1);
        });
        this.deleteData = arr;
      }
    },
    //删除操作
    delete() {
      DeleteBox(this.deleteData).then((res) => {
        if (res.success) {
          this.$message.success("删除成功");
        }
      });
    },
    //校验
    validation(){
      try{
      this.tableData.forEach((item,index)=>{
        for(var i in item){
          if(item[i]===""){
            this.$message.error("第"+(index+1)+"行含有空字符串");
            this.isContainBlank = true;
            throw new Error('End Loop');
          }
          else{
            this.isContainBlank = false;
          }
        }
      })}catch(e){
        //catch语句跳出forEach循环
      }
    },
    //提交操作
    submit(){
      //校验有没有空数据
      this.validation();
      console.log('1111')
      if(!this.isContainBlank){
        this.delete();
        InsertOrUpdateBox(this.tableData).then(res=>{
          if(res.success){
            this.$router.push("/box")
          }
        });
      }
    },
    //取消
    cancel(){
      this.$router.go(-1);
    },
      handlePictureCardPreview(file){
          this.dialogImageUrl = file.url;
          this.dialogVisible = true
      }
  }
};
</script>

<style scoped>
.el-row {
  margin-bottom: 10px;
}
</style>