import {request} from './request'

//获得箱子采购单
export async function GetAllBoxs(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllBoxs',
        data:data
    })
}
//增加箱子采购单
export async function InsertOrUpdateBox(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/InsertOrUpdateBox',
        data:data
    })
}
//获得相关ID箱子采购单
export async function GetRelatedBoxs(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetRelatedBoxs',
        data:data
    })
}
//删除箱子采购单
export async function DeleteBox(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteBox',
        data:data
    })
}
